*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  margin-left: calc(100vw - 100%);
  margin-right: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  letter-spacing: .03em;
  letter-spacing: 0;
  cursor: default;
  background: #000;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
}

body.no-scroll {
  overflow: hidden;
}

a, button {
  cursor: pointer;
  color: inherit;
  font-style: normal;
  text-decoration: none;
}

ul {
  list-style: none;
}

h1, h2, h3, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --footer-bg-cl: #f7f7f7;
  --bg-color-header: #ff001b;
  --bg-col-current: #ff6b08;
  --footer-second-bg-cl: #fff;
  --footer-text-cl: #545454;
  --header-text-card: #000;
  --error-text: #ff001b;
  --footer-img-cl: #ff6b08;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

.simplifytotop {
  visibility: hidden;
  z-index: 2;
  color: #fff;
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  background: #ff6b08;
  border-radius: 99em;
  font-size: 12px;
  line-height: 50px;
  transition: all .3s;
  position: fixed;
  top: 70%;
  right: 10%;
  transform: translateZ(0)scale(1);
  box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
}

@media screen and (min-width: 768px) {
  .simplifytotop {
    top: 80%;
    right: 7%;
  }
}

@media screen and (min-width: 1200px) {
  .simplifytotop {
    bottom: 30px;
    right: calc(50vw - 590px);
  }
}

.isShowBtn {
  visibility: visible;
  opacity: 1;
  transition: all .3s;
}

.isShowBtn_hide {
  opacity: 0;
  visibility: hidden;
  transition: all .2s;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.main {
  min-height: calc(100vh - 215px);
}

@media screen and (min-width: 768px) {
  .main {
    min-height: calc(100vh - 175px);
  }
}

.btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0 5px;
  display: inline-flex;
}

.btn:hover {
  text-decoration: underline;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  gap: 20px;
  transition: opasity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__team {
  width: 90%;
  height: 90%;
  background-color: #f7f7f7;
  border-radius: 4px;
  margin: auto;
  padding: 38px 20px 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  overflow: auto;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal__team {
    width: 738px;
  }
}

@media screen and (min-width: 1200px) {
  .modal__team {
    width: 960px;
  }
}

.modal__btn {
  width: 48px;
  height: 48px;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60%;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 2%;
  right: 2%;
}

.modal__btn:hover .modal__icon, .modal__btn:focus .modal__icon {
  fill: var(--footer-img-cl);
}

.modal__team-title {
  text-align: center;
  color: var(--footer-img-cl);
  font-weight: 900px;
  margin-bottom: 10px;
  font-size: 40px;
}

.modal__team-info {
  max-width: 100%;
  grid-template-columns: 1fr;
  row-gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  list-style: none;
  display: grid;
}

@media screen and (min-width: 768px) {
  .modal__team-info {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 10px;
  }
}

@media screen and (min-width: 1200px) {
