.nav_link:before {
  content: " ";
  width: 100%;
  height: 2px;
  background-color: var(--bg-color-header);
  transform-origin: 0;
  transition: transform .35s;
  position: absolute;
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
}

.nav_link:hover:before {
  width: 100%;
  transform: scaleX(1);
}

.nav_link:not(:last-child) {
  margin-right: 40px;
}

.active-header:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--bg-color-header);
  border-radius: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
}

