.modal__team-title {
  text-align: center;
  color: var(--footer-img-cl);
  font-weight: 900px;
  font-size: 40px;
  margin-bottom: 10px;
}

.modal__team-info {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr;
  max-width: 100%;
    margin-left: auto;
    margin-right: auto;
   padding: 10px;
          row-gap: 20px;
   
        @media screen and (min-width: 768px) 
        {
          grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
          // padding: 60px 32px;
          row-gap: 10px;
          column-gap: 10px;
        }
        @media screen and (min-width: 1200px) {
           column-gap: 10px; 
        }
  }

.list__team {
  list-style: none;
  position: relative;
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  &:hover .list__team-link, 
  &:focus .list__team-link{
    opacity: 1;
    background-color: rgba(255, 107, 8, 0.9);
        }

  &-img {
    object-fit: cover;
    // min-width: 100%;
    border-radius: 50%;
    height: 150px;
    width: 150px;
    margin: auto;
    margin-bottom: 5px;
    transition: 1s;
    &:hover {
      transform: scale(1.05);
    }
   }

  &-wrapper {
    color: var(--footer-img-cl);

    text-align: center;
    height: auto;
  }

  &-title {
    color: black;
    text-align: center;
    margin-top: 10px;
    line-height: 1.12;
    font-weight: 900px;
    // font-size: 12px;
  }

  &-subtitle {    text-transform: uppercase;
    font-size: 10px;
    text-align: center;  margin-bottom: 5px;
    @media screen and (min-width: 768px) and (max-width: 1199px) {
              line-height: 1.15px;
    }
    @media screen and (min-width: 1200px) {
    
      line-height: 1.1px;
    }
  }
}

.list__team-link {
  display: flex;  
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  border-radius: 50%;
  opacity: 0;
  height: 150px;
  width: 150px;
  justify-content: center;
  align-items: center;
  padding-top: 5px;
  color: inherit;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.list__team-link:hover,
:active,
:focus {
  color: var(--footer-img-cl);
  outline: none;
  cursor: pointer;
}

.list__team-icon {
  display: inline-block;
  height: 90px;
  width: 90px;
  background-repeat: no-repeat;
  background-size: 90px;
  background-position: center;
  border-radius: 50%;
  background-image: url('https://github.com/AntoninaDanylchenko/team-project-js/raw/main/src/images/team-photo/github-icon.png');
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.list__team-icon:hover {
  box-shadow: 0px 1px 3px rgba(255, 107, 1, 0.12),
    0px 1px 1px rgba(255, 107, 1, 0.14), 0px 5px 10px 2px rgba(255, 107, 1, 0.2),
    0px 2px 1px rgba(255, 107, 1, 0.2);
}
